@charset "utf-8";

@mixin mb($size, $sp_size: false) {
  margin-bottom: $size * 1px;
  &:last-child {
    margin-bottom: 0; }

  @if $sp_size {
    @include mq(sp) {
      margin-bottom: $sp_size * 1px;
      &:last-child {
        margin-bottom: 0; } } } }

@mixin mr($size, $sp_size: false) {
  margin-right: $size * 1px;
  &:last-child {
    margin-right: 0; }

  @if $sp_size {
    @include mq(sp) {
      margin-right: $sp_size * 1px;
      &:last-child {
        margin-right: 0; } } } }

@mixin ml($size, $sp_size: false) {
  margin-left: $size * 1px;
  &:first-child {
    margin-left: 0; }

  @if $sp_size {
    @include mq(sp) {
      margin-left: $sp_size * 1px;
      &:first-child {
        margin-left: 0; } } } }

@mixin mt($size, $sp_size: false) {
  margin-top: $size * 1px;
  &:first-child {
    margin-top: 0; }

  @if $sp_size {
    @include mq(sp) {
      margin-top: $sp_size * 1px;
      &:first-child {
        margin-top: 0; } } } }

@mixin mtb($size, $sp_size: false) {

  &:not(:first-child) {
    margin-top: $size * 1px; }
  &:not(:last-child) {
    margin-bottom: $size * 1px; }

  @if $sp_size {
    @include mq(sp) {
      &:not(:first-child) {
        margin-top: $sp_size * 1px; }
      &:not(:last-child) {
        margin-bottom: $sp_size * 1px; } } } }

