@charset "utf-8";


// media query
//------------------------------------------------------------

@mixin mq-under($breakpoint) {
  @media screen and (max-width: $breakpoint) {
    @content; } }

@mixin mq-over($breakpoint) {
  @media screen and (min-width: $breakpoint + 1) {
    @content; } }

@mixin mq-between($breakpointSmall, $breakpointLarge) {
  @media screen and (min-width: $breakpointSmall + 1) and (max-width: $breakpointLarge) {
    @content; } }

// media query
//------------------------------------------------------------
@mixin mq($point) {
  @if $point == pc {
    @include mq-over($breakpoint) {
      @content; } }
  @else if $point == sp {
    @include mq-under($breakpoint) {
      @content; } } }

@mixin print {
  @media print {
    @content; } }

@mixin apple {
  .iphone &, .ipod &, .ipad &, .mac & {
    @content; } }

