@charset "utf-8";

@mixin rem($size: 1, $sp_size: false) {
  // $ratio: 1
  $ratio: 0.625;
  font-size: $size * $ratio * 1rem;

  @if $sp_size {
    @include mq(sp) {
      font-size: $sp_size * $ratio * 1rem; } } }

@mixin line-height($size: 1, $sp_size: false) {
  $ratio: 0.625;
  line-height: $size * $ratio * 1rem;
  @if $sp_size {
    @include mq(sp) {
      line-height: $sp_size * $ratio * 1rem; } } }

@mixin fz($size: 1, $sp_size: false) {
  font-size: $size * 0.625 * 1rem;

  @if $sp_size {
    @include mq(sp) {
      font-size: $sp_size * 0.63 * 1rem; } } }
