@charset "utf-8";

@mixin noto {
  font-family: "Noto Sans Japanese", "Noto Sans", "Noto Sans CJK JP", sans-serif; }

@mixin ttl {
  font-family: 'source-han-serif-japanese', serif; }

@mixin letter($size: 1) {
  font-feature-settings: "palt" $size, "jp90";

  .safari & {
    font-feature-settings: "pkna" 1; } }
